.radio {
    appearance: none;
    width: 18px;
    height: 18px;
    border: 2px solid #ccc;
    border-radius: 50%;
    outline: none;
    cursor: pointer;
    transition: border-color 0.3s, box-shadow 0.3s;
    margin-right: 8px;
}

.radio::before {
    content: '';
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: transparent;
    margin: 3px;
    transition: background-color 0.3s;
}

.radio:checked::before {
    background-color: #007bff;
}

.label {
    display: flex;
    align-items: center;
    margin-bottom: 12px;
    cursor: pointer;
}

.span {
    font-size: 16px;
    color: #333;
}

.options {
    display: flex;
    flex-direction: column;
}