.item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    border-bottom: 1px solid #e0e0e0;
    transition: background-color 0.3s ease;
}

.item:last-of-type {
    border-bottom: none;
}

.item:hover {
    background-color: #f5f5f5;
}

.users {
    width: 100%;
    border-collapse: collapse;
    font-size: 16px;
    color: #333;
}

.buttonsContainer {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.buttonsContainer button {
    margin: 0 10px;
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.buttonsContainer button:hover {
    background-color: #4551a0;
}

.active {
    background-color: #7e1b50;
}


.container {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    padding: 30px;
}

.thead {
    background-color: #f5f5f5;
    font-weight: bold;
}

.nameCol {
    width: 50%;
    text-align: left;
    padding: 10px 20px;
}

.ageCol {
    width: 50%;
    text-align: center;
    padding: 10px 20px;
}